import androidx.compose.runtime.Composable
import androidx.compose.runtime.ProvidableCompositionLocal
import androidx.compose.runtime.compositionLocalOf
import com.varabyte.kobweb.core.AppGlobals
import com.varabyte.kobweb.navigation.BasePath
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.navigation.remove
import com.varabyte.kobweb.silk.defer.DeferringHost
import kotlin.Any
import kotlin.Unit
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable
import com.indusjs.statustracker.components.layouts.MarkdownLayout as _com_indusjs_statustracker_components_layouts_MarkdownLayout
import com.indusjs.statustracker.components.layouts.PageLayout as _com_indusjs_statustracker_components_layouts_PageLayout
import com.indusjs.statustracker.pages.AboutPage as _com_indusjs_statustracker_pages_AboutPage
import com.indusjs.statustracker.pages.DailyWorkLogPage as _com_indusjs_statustracker_pages_DailyWorkLogPage
import com.indusjs.statustracker.pages.HomePage as _com_indusjs_statustracker_pages_HomePage
import com.indusjs.statustracker.pages.LoginPage as _com_indusjs_statustracker_pages_LoginPage
import com.indusjs.statustracker.pages.TestEventListenerPage as _com_indusjs_statustracker_pages_TestEventListenerPage
import com.indusjs.statustracker.pages.WorkLogListPage as _com_indusjs_statustracker_pages_WorkLogListPage

private val LayoutScopeLocal: ProvidableCompositionLocal<Any?> = compositionLocalOf<Any?> { null }

@Composable
private fun <T : Any> currentLayoutScope(): T {
    @Suppress("UNCHECKED_CAST")
    return LayoutScopeLocal.current as? T
        ?: error("Unexpected between Page/Layout scope and the parent Layout which it is providing it. Please report this issue to the Kobweb developers.")
}

@Composable
private fun <T : Any> provideLayoutScope(layoutScope: T, content: @Composable () -> Unit) {
    androidx.compose.runtime.CompositionLocalProvider(LayoutScopeLocal provides layoutScope, content = content)
}

public fun main() {
    AppGlobals.initialize(mapOf("title" to "StatusTracker"))
    BasePath.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.registerLayout(
            "com.indusjs.statustracker.components.layouts.MarkdownLayout",
            parentLayoutId = "com.indusjs.statustracker.components.layouts.PageLayout",
            initRouteMethod = { ctx ->
                com.indusjs.statustracker.components.layouts.initMarkdownLayout(ctx)
            },
        ) { pageCtx, pageMethod -> 
            _com_indusjs_statustracker_components_layouts_MarkdownLayout {
                pageMethod(pageCtx)
            }
        }
        ctx.router.registerLayout(
            "com.indusjs.statustracker.components.layouts.PageLayout",
        ) { pageCtx, pageMethod -> 
            _com_indusjs_statustracker_components_layouts_PageLayout(pageCtx) {
                provideLayoutScope(this) {
                    pageMethod(pageCtx)
                }
            }
        }
        ctx.router.register(
            "/",
            layoutId = "com.indusjs.statustracker.components.layouts.PageLayout",
            initRouteMethod = { ctx ->
                com.indusjs.statustracker.pages.initHomePage(ctx)
            },
        ) { pageCtx -> 
            _com_indusjs_statustracker_pages_HomePage(pageCtx)
        }
        ctx.router.register(
            "/about",
            layoutId = "com.indusjs.statustracker.components.layouts.MarkdownLayout",
            initRouteMethod = { ctx ->
                com.indusjs.statustracker.pages.initAboutPage(ctx)
            },
        ) { pageCtx -> 
            _com_indusjs_statustracker_pages_AboutPage()
        }
        ctx.router.register(
            "/dailyworklog",
        ) { pageCtx -> 
            _com_indusjs_statustracker_pages_DailyWorkLogPage(pageCtx)
        }
        ctx.router.register(
            "/dashboard",
        ) { pageCtx -> 
            _com_indusjs_statustracker_pages_WorkLogListPage(pageCtx)
        }
        ctx.router.register(
            "/login",
            initRouteMethod = { ctx ->
                com.indusjs.statustracker.pages.initLoginPage(ctx)
            },
        ) { pageCtx -> 
            _com_indusjs_statustracker_pages_LoginPage(pageCtx)
        }
        ctx.router.register(
            "/test_events",
        ) { pageCtx -> 
            _com_indusjs_statustracker_pages_TestEventListenerPage()
        }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.additionalSilkInitialization = { ctx ->
        com.varabyte.kobweb.silk.init.initSilkWidgets(ctx)
        com.varabyte.kobweb.silk.init.initSilkWidgetsKobweb(ctx)
        ctx.theme.registerStyle("headline-text", com.indusjs.statustracker.HeadlineTextStyle)
        ctx.theme.registerStyle("subheadline-text", com.indusjs.statustracker.SubheadlineTextStyle)
        ctx.theme.registerStyle("markdown", com.indusjs.statustracker.components.layouts.MarkdownStyle)
        ctx.theme.registerStyle("page-content", com.indusjs.statustracker.components.layouts.PageContentStyle)
        ctx.theme.registerStyle("footer", com.indusjs.statustracker.components.sections.FooterStyle)
        ctx.theme.registerStyle("nav-header", com.indusjs.statustracker.components.sections.NavHeaderStyle)
        ctx.theme.registerStyle("hero-container", com.indusjs.statustracker.pages.HeroContainerStyle)
        ctx.theme.registerStyle("home-grid", com.indusjs.statustracker.pages.HomeGridStyle)
        ctx.theme.registerStyle("home-grid-cell", com.indusjs.statustracker.pages.HomeGridCellStyle)
        ctx.theme.registerVariant("-circle", com.indusjs.statustracker.CircleButtonVariant
        )
        ctx.theme.registerVariant("-uncolored", com.indusjs.statustracker.UncoloredButtonVariant
        )
        ctx.theme.registerKeyframes("side-menu-slide-in", com.indusjs.statustracker.components.sections.SideMenuSlideInAnim)
        com.indusjs.statustracker.initColorMode(ctx)
        com.indusjs.statustracker.initStyles(ctx)
        com.indusjs.statustracker.initSiteStyles(ctx)
        com.indusjs.statustracker.initTheme(ctx)
    }

    router.tryRoutingTo(BasePath.remove(window.location.href.removePrefix(window.origin)), UpdateHistoryMode.REPLACE)

    // For SEO, we may bake the contents of a page in at build time. However, we will
    // overwrite them the first time we render this page with their composable, dynamic
    // versions. Think of this as poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("_kobweb-root")!!
    while (root.firstChild != null) { root.removeChild(root.firstChild!!) }

    renderComposable(rootElementId = "_kobweb-root") {
        com.indusjs.statustracker.AppEntry {
            router.renderActivePage { DeferringHost { it() } }
        }
    }
}
